html {
    font-size: 6px;
    height: 100%;
}

body {
    font-family: 'Poppins', sans-serif;
    font-size: 2rem;

    min-width: 300px;
    height: 100%;
}

ul {
    list-style: none;
}

.p-section {
    min-height: 100vh;
}

.p-container {
    max-width: $container-max-width;
    margin: 0 auto;
}

.p-title {
    font-weight: 500;
    font-size: 1.8em;
    text-align: center;
    margin: 90px 0 70px 0;
}

.p-text-bold {
    font-weight: 700;
}

.p-borderBottom50 {
    position: absolute;
    bottom: 0;
    left: 25%;
    right: 25%;
    border-bottom: 4px solid #999;
}

/* Larger than mobile */

@media (min-width: 400px) {
    html {
        font-size: 6px;
    }
}


/* Larger than phablet */

@media (min-width: 550px) {
    html {
        font-size: 7px;
    }
}



/* Larger than tablet */

@media (min-width: 750px) {

    html {
        font-size: 8px;
    }

    .p-container {
        max-width: 90%;
    }
}


/* Larger than desktop */

@media (min-width: 1000px) {
    html {
        font-size: 10px;
    }
}


/* Larger than Desktop HD */

@media (min-width: 1200px) {
    .p-container {
        max-width: 80%;
    }
}