.p-projects {
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 11;
    background-color: #FFF;

    .p-project {
        display: flex;

        &:nth-child(odd) {
            flex-direction: row-reverse;

            .p-timeline {
                border-left: 4px #FFF dashed;
                border-left: 4px rgba(255, 255, 255, 0.4) dashed;
                margin-left: 10px;
            }

            .p-timeline-bb {
                border-left: 4px #000 dashed;
                border-left: 4px rgba(000, 000, 000, 0.4) dashed;
            }

            .p-description {
                justify-content: flex-end;
            }
        }

        &:nth-child(even) {

            .p-timeline {
                align-items: flex-end;

                border-right: 4px #FFF dashed;
                border-right: 4px rgba(255, 255, 255, 0.4) dashed;
                margin-right: 10px;
            }

            .p-timeline-bb {
                border-right: 4px #000 dashed;
                border-right: 4px rgba(000, 000, 000, 0.4) dashed;
            }
        }

        .p-name,
        .p-description {
            display: flex;
            flex: 1 1 0;

            &:nth-child(1) {
                justify-content: flex-end;
            }
        }

        .p-timeline {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 0 10px;
            margin: 20px 0;
            font-size: 1.4rem;
            font-weight: 600;

            .p-company {
                align-self: center;
                font-size: 4rem;

                writing-mode: vertical-rl;
            }
        }

        .p-description {
            font-size: 1.8rem;
            line-height: 3rem;
            text-align: justify;
            align-items: center;

            >p {
                margin: 90px 20px;
            }
        }

        .p-timeline {
            width: 100%;
            flex-basis: 100%;
            max-width: $container-max-width;
        }
    }

}

.p-name {
    .p-laposte & {
        background-color: $p-laposte-bg-color;
        color: $p-laposte-color;
    }

    .p-chanel & {
        background-color: $p-chanel-bg-color;
        color: $p-chanel-color;
    }

    .p-bnp-paribas & {
        background-color: $p-bnp-paribas-bg-color;
        color: $p-bnp-paribas-color;

    }

    .p-seb & {
        background-color: $p-seb-bg-color;
        color: $p-seb-color;

    }

    .p-sqli & {
        background-color: $p-sqli-bg-color;
        color: $p-sqli-color;

    }

}




/* Larger than mobile */

@media (min-width: 400px) {}


/* Larger than phablet */

@media (min-width: 550px) {

    .p-projects {

        .p-project {
            .p-timeline {

                .p-company {
                    writing-mode: initial;
                }
            }
        }
    }
}



/* Larger than tablet */

@media (min-width: 750px) {

    .p-project {
        .p-description {
            font-weight: 300;
        }
    }
}


/* Larger than desktop */

@media (min-width: 1000px) {}


