.p-footer {

    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 10rem;
    background-color: #26262a;
    color: white;
    text-align: center;

    .p-disclaimer {
        font-size: 1.3rem;
        color: rgba(255, 255, 255, 0.7);
        position: relative;
    }

    .p-copyright {
        font-size: 1.5rem;
        border-top: 1px solid rgba(255, 255, 255, 0.7);
        color: rgba(255, 255, 255, 0.8);
        padding-top: 10px;
    }

}


