.p-about {

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;


    .p-content {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        gap: 3rem;
        line-height: 4rem;
        padding: 0 10px;

        .p-photo {
            border-radius: 50%;
            min-width: 200px;
            max-width: 30rem;
            padding: 0 20px;
            height: auto;
        }

        .p-bio {
            text-align: center;
            padding: 0 20px;

            .p-focus {
                margin-top: 30px;
            }
        }
    }

}



/* Larger than mobile */

@media (min-width: 400px) {}


/* Larger than phablet */

@media (min-width: 550px) {

    .p-about {
        .p-content {
            .p-bio {
                text-align: left;
            }
        }
    }

}



/* Larger than tablet */

@media (min-width: 750px) {}


/* Larger than desktop */

@media (min-width: 1000px) {
    .p-about {
        .p-content {
            flex-wrap: nowrap;
            font-size: 1.7rem;
        }
    }
}


/* Larger than Desktop HD */

@media (min-width: 1200px) {}