.p-main {
    position: relative;
}

.p-navigation {
    margin: 0;
    padding: 0;
    width: 100%;
    position: sticky;
    top: 0;
    background-color: #FFF;
    z-index: 10;

    display: flex;
    justify-content: center;
    gap: 20px;

    a {
        display: inline-flex;
        text-decoration: none;
        color: #000;
        font-size: 1.8rem;
        font-weight: 500;
        padding: 15px 10px 10px 10px;
        border-bottom: #000 1px solid;


        &:hover {
            background-color: #000;
            color: #FFF;
        }
    }
}


/* Larger than mobile */

@media (max-width: 300px) {
    .p-navigation {
        flex-wrap: wrap;
    }
}