@import "intro-animation";
@import "intro-decoration";

.p-intro {
    background-color: #111;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
    min-height: 100%;
    /* Fix Mobile device navigation bar */

    .p-craft {
        display: none;
        color: #444;
        text-align: center;
        padding: 0;
        font-weight: 800;

        flex: 1 1 0px;

        li {
            margin: 80px 0;
            opacity: 0.2;
        }
    }

    .p-welcome {
        position: relative;
        color: #FFF;
        font-size: 8rem;
        font-weight: 200;
        display: grid;
        grid-template-columns: max-content;
        justify-content: center;
        padding: 0;

        flex: 1 1 0px;
    }

    .p-bg-white {
        color: #111;
        background-color: #F0F0F0;

    }

    .p-scroll {
        position: absolute;
        bottom: 3rem;
        left: 50%;
        transform: translateX(-50%);
        color: #FFF;
        opacity: 0.8;
    }
}


/* Larger than mobile */

@media (min-width: 400px) {}


/* Larger than phablet */

@media (min-width: 550px) {}



/* Larger than tablet */

@media (min-width: 750px) {

    .p-intro {
        .p-craft {
            display: block;
            font-size: 4rem;
        }

        .p-welcome {
            justify-content: start;
        }
    }
}


/* Larger than desktop */

@media (min-width: 1000px) {}


/* Larger than Desktop HD */

@media (min-width: 1200px) {
    .p-intro {
        .p-craft {
            font-size: 5rem;
        }
    }
}