@keyframes animate-logos {

    0%,
    .9% {
        transform: translateX(0) rotate(0);
    }

    1%,
    8% {
        transform: translateX(-4rem) rotate(-90deg);
    }

    9%,
    16% {
        transform: translateX(-3rem) rotate(-67.5deg);
    }

    17%,
    24% {
        transform: translateX(-2rem) rotate(-45deg);
    }

    25%,
    33% {
        transform: translateX(-1rem) rotate(-22.5deg);
    }

    34%,
    100% {
        transform: translateX(0) rotate(0);
    }
}

.p-contact {

    .p-channels {

        li:first-child {
            animation: animate-logos 1.8s ease-in-out .6s;
        }

        li:nth-child(2) {
            animation: animate-logos 1.8s ease-in-out 1.2s;
        }

        li:nth-child(3) {
            animation: animate-logos 1.8s ease-in-out 1.8s;
        }
    }
}