.p-intro {

    .p-welcome {

        .p-circle-decoration {
            position: absolute;
            border-radius: 100%;
            opacity: 0.8;

            &:nth-child(5) {
                height: 4rem;
                width: 4rem;
                background-color: #F00;
                left: 70%;
                bottom: -10%;
            }

            &:nth-child(6) {
                height: 2.7rem;
                width: 2.7rem;
                background-color: #FF0;
                left: 80%;
                top: -20%;
            }

            &:nth-child(1) {
                height: 2rem;
                width: 2rem;
                background-color: #00F;
                left: 22rem;
                top: 7rem;
            }

        }
    }

}