@import "skills-animation";

.p-skills {

    display: flex;
    flex-direction: column;
    justify-content: center;

    .p-title {
        margin-bottom: 0;
    }

    .p-content {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column-reverse;
        row-gap: 20px;
        position: relative;
        z-index: 11;
        background-color: #FFF;
        padding-top: 30px;
    }

    .p-technologies,
    .p-competencies {
        flex: 1 1 0;

        display: flex;
        align-items: center;
    }

    .p-technologies {
        justify-content: space-evenly;

        background-color: #2C9E65;
        color: #FFF;
        text-align: center;

        ul {
            padding: 0;
            line-height: 4rem;
        }

        li {
            margin-top: 20px;
            text-shadow: 1px 1px 4px #000 ;
        }
    }

    .p-competencies {
        border-left: 3px solid #707070;
        margin: 30px;
        font-size: 2rem;
        font-weight: 300;

        ul {
            list-style: disc;
        }

        li {
            margin-bottom: 30px;

            &:last-child {
                margin-bottom: 0px;
            }
        }
    }
}



/* Larger than mobile */

@media (min-width: 400px) {}


/* Larger than phablet */

@media (min-width: 550px) {}



/* Larger than tablet */

@media (min-width: 750px) {
    .p-skills {
        .p-title {
            margin-bottom: 70px;
        }

        .p-content {
            flex-wrap: nowrap;
            flex-direction: row;
            padding-top: 0px;
        }

        .p-technologies {
            margin-right: 20px;
        }

        .p-competencies {
            margin: 0;
            font-size: 1.7rem;

            ul {
                margin: 0;
            }
        }
    }
}


/* Larger than desktop */

@media (min-width: 1000px) {
    .p-skills {

        .p-competencies {
            font-size: 1.6rem;
        }
    }
}


/* Larger than Desktop HD */

@media (min-width: 1200px) {
    .p-skills {

        .p-competencies {
            font-size: 1.7rem;
        }
    }
}