@import "contact-animation";

.p-contact {

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    .p-content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        row-gap: 5rem;
        padding-bottom: 70px;
    }

    .p-heading {
        font-weight: 300;
        line-height: 4.5rem;
        font-size: 2.4rem;
        flex: 1 1 0;
        padding: 2rem;
        margin: 0;
        position: relative;
        text-align: center;
    }

    .p-channels {
        flex: 1 1 0;
        row-gap: 3rem;
        margin: 0;
        padding: 0;

        display: flex;
        flex-direction: column;
        align-items: center;

        li {
            background-color: #252429;
            padding: 1rem 2rem;
            border-radius: 1rem;
            width: 100%;
            text-align: center;
        }

        a {
            text-decoration: none;
            font-size: 2.6rem;
            color: white;
            font-weight: 500;
        }

        img {
            width: 5rem;
            height: auto;
            vertical-align: middle;
            margin-right: 1rem;
        }

        li:hover {
            transform: scale(1.1);
        }
    }

    .p-borderBottom50 {
        border-bottom: 2px solid #454449;
    }

}


/* Larger than mobile */

@media (min-width: 400px) {}


/* Larger than phablet */

@media (min-width: 550px) {}



/* Larger than tablet */

@media (min-width: 750px) {

    .p-contact {

        .p-heading {
            font-size: 2rem;
        }

        .p-channels {

            a {
                font-size: 1.8rem;
            }
        }
    }
}


/* Larger than desktop */

@media (min-width: 1000px) {
    .p-contact {

        .p-heading {
            font-size: 2rem;
        }

        .p-channels {
            width: 100%;
            justify-content: space-around;
            flex-direction: row;

            li {
                width: auto;
                padding: 0.8rem 1.5rem;
            }

            a {
                font-size: 1.5rem;
            }

            img {
                width: 4rem;
                vertical-align: middle;
                margin-right: 1rem;
            }
        }
    }
}


