@keyframes moving_techs {
    from {
        transform: scale(0.6);
    }

    to {
        transform: scale(1);
    }
}


.p-skills {

    .p-technologies {
        li:nth-child(2n+1) {
            animation: 4s ease 4s infinite alternate moving_techs;
        }

        li:nth-child(2n+2) {
            animation: 4s ease 8s infinite alternate moving_techs;
        }
    }
}