@keyframes animate-greeting {
    from {
        transform: translateY(-100%) scale(0.3);
        opacity: 0;
    }

    to {
        transform: translateY(0) scale(1);
        opacity: 1;
    }
}

@keyframes animate-craft {
    0%, .9% {
        opacity: 0.2;
    }
    1%, 33% {
        opacity: 1;
    }
    34%, 100% {
        opacity: 0.2;
    }
}

@keyframes animate-scroll {
    from {
        transform: translateY(-10px) scale(0.8);
        opacity: 0.7;
    }

    to {
        transform: translateY(0) scale(1);
        opacity: 0.9;
    }
}

.p-intro {

    .p-craft {

        li:first-child {
            animation: animate-craft 3s linear 1s infinite;
        }

        li:nth-child(2) {
            animation: animate-craft 3s linear 2s infinite;
        }

        li:nth-child(3) {
            animation: animate-craft 3s linear 3s infinite;
        }
    }

    .p-welcome {

        li:first-child {
            opacity: 0;
            animation: animate-greeting 1s ease-in-out .2s forwards;
        }

        li:nth-child(2),
        li:nth-child(3),
        li:nth-child(4) {
            opacity: 0;
            animation: animate-greeting 1.5s ease-in-out 1s forwards;
        }

    }

    .p-scroll {
        animation: animate-scroll 1s linear .9s infinite;
    }
}